import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UsergymmoduleComponent } from './usergymmodule.component';
import { routing } from './usergymmodule.routing';
import { SlickModule } from 'ngx-slick';
import { CoreModule } from 'core/core.module';
import { CoreModuleNew } from 'newcore/new-core.module';

@NgModule({
  imports: [
    CommonModule,
    routing,
    CoreModule,
    SlickModule.forRoot(),
    CoreModuleNew

  ],
  declarations: [UsergymmoduleComponent],
  exports: [UsergymmoduleComponent]
})
export class UsergymmoduleModule { }



