import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { Modal } from 'angular2-modal';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { Constants } from 'core/constants';
import { UserSession } from 'core/sessions';
import * as _ from 'underscore';

@Component({
  selector: 'app-usergymmodule',
  templateUrl: './usergymmodule.component.html',
  styleUrls: ['./usergymmodule.component.css']
})
export class UsergymmoduleComponent implements OnInit,HttpSuccesFailureResponse {
  isLoadMoreData: boolean;
  allGymList: AllGymResponseDetail[]= [];
  lastScrollDistance: number;
  catagoryId=0;
  currentPage=0;
  temp: any;
  activitiesListDataResponse: any;
  slideConfig: any;
  GymListisLoading=false;

  
  constructor(private http_user: HttpUserAuthUtilsService, private http_common: HttpCommonUtilsService,
    private modal: Modal,private router: Router) { }

  ngOnInit() {
    this.getActivitiesList();
    this.GymListisLoading = true;
    this.gymList(this.catagoryId, true);

    this.slideConfig = {
      slidesToShow: 8,
      slidesToScroll: 1,
      infinite: false,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 6,
            slidesToScroll: 1,
            infinite: true,
            dots: false
          }
        },
        {
          breakpoint: 767,
          settings: {
            slidesToShow: 6,
            slidesToScroll: 1,
            dots: false

          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
            dots: false

          }
        }
      ]
    };

  }

  getActivitiesList() {
    this.http_common.getAllActivity(this, false, 1);
  }

  gymList(categoryid, showLoader) {
    this.temp = categoryid;
    this.GymListisLoading = true;
    if (showLoader) {
      this.resetBeforeLoad();

    }
    this.catagoryId = categoryid;
    this.http_user.getAllGymList(this.catagoryId,this.currentPage, this,showLoader);
  }

  resetBeforeLoad() {
    this.currentPage = 0;
    this.isLoadMoreData = true;
    this.allGymList = [];
    this.lastScrollDistance = 0;
  }

  onScrollDown() {
    if (this.isLoadMoreData && !this.GymListisLoading) {
      this.currentPage = this.currentPage + 1;
      this.gymList(this.catagoryId, false);

    }
  }
  onScroll() {

  }

  routeToGymDetails(value: AllGymResponseDetail) {
    sessionStorage.setItem('gym_id', value.id + '');
    this.router.navigate(['/gym', value.id, value.gymNames.replace(/ /g, '')]);
  }


  onSuccess(type: any, responsedata: any) {
    if(type == Constants.AllGymAPI) {
      this.GymListisLoading = false;
      const temp_list: AllGymResponseDetail[] = responsedata.data;
      temp_list.forEach(element => {
        element.trainsAt = this.getTrainsAt(element.trainerBranchResponseList);
        this.allGymList.push(element);
      });
console.log(this.allGymList);
    }

    if (type == Constants.AllActivityListApi) {
      this.activitiesListDataResponse = UserSession.setStatusForList(responsedata.data);
    }
  }
 getTrainsAt(trainerBranchResponseList:TrainerBranchResponseList[]):string{
   let trainsAt="";
  
       var filterList =  _.uniq(trainerBranchResponseList, x => x.localityId);
filterList.forEach(element => {
  trainsAt= trainsAt+element.localityName+", ";
   
 });
//  console.log("XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX "+trainsAt.substring(0, trainsAt.length-1));
 return trainsAt.substring(0, trainsAt.length-1);
}


  onFailure(type: any, response: any) {
    if(type == Constants.AllGymAPI) { 

    }
    if (type == Constants.AllActivityListApi) {

    }

  }

}



